.home__loader-spinner .lds-spinner {
  color: official;
  display: inline-block;
  position: relative;
  width: 8rem;
  height: 8rem;
}
.home__loader-spinner .lds-spinner div {
  transform-origin: 4rem 4rem;
  animation: lds-spinner 1.2s linear infinite;
}
.home__loader-spinner .lds-spinner div:after {
  content: " ";
  display: block;
  position: absolute;
  top: 0.3rem;
  left: 3.7rem;
  width: 0.6rem;
  height: 1.8rem;
  border-radius: 20%;
  background: #ee2d24;
  border-radius: 0.5rem;
}
.home__loader-spinner .lds-spinner div:nth-child(1) {
  transform: rotate(0deg);
  animation-delay: -1.1s;
}
.home__loader-spinner .lds-spinner div:nth-child(2) {
  transform: rotate(30deg);
  animation-delay: -1s;
}
.home__loader-spinner .lds-spinner div:nth-child(3) {
  transform: rotate(60deg);
  animation-delay: -0.9s;
}
.home__loader-spinner .lds-spinner div:nth-child(4) {
  transform: rotate(90deg);
  animation-delay: -0.8s;
}
.home__loader-spinner .lds-spinner div:nth-child(5) {
  transform: rotate(120deg);
  animation-delay: -0.7s;
}
.home__loader-spinner .lds-spinner div:nth-child(6) {
  transform: rotate(150deg);
  animation-delay: -0.6s;
}
.home__loader-spinner .lds-spinner div:nth-child(7) {
  transform: rotate(180deg);
  animation-delay: -0.5s;
}
.home__loader-spinner .lds-spinner div:nth-child(8) {
  transform: rotate(210deg);
  animation-delay: -0.4s;
}
.home__loader-spinner .lds-spinner div:nth-child(9) {
  transform: rotate(240deg);
  animation-delay: -0.3s;
}
.home__loader-spinner .lds-spinner div:nth-child(10) {
  transform: rotate(270deg);
  animation-delay: -0.2s;
}
.home__loader-spinner .lds-spinner div:nth-child(11) {
  transform: rotate(300deg);
  animation-delay: -0.1s;
}
.home__loader-spinner .lds-spinner div:nth-child(12) {
  transform: rotate(330deg);
  animation-delay: 0s;
}
@keyframes lds-spinner {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

